import App from '../app';

/**
 * Коллбеки принимают data, обрабатывают ее и должны вернуть
 * @param {object} event - событие формы
 * @param {object} options - опции ajax запроса
 * @param {function} onSuccess(data) - коллбек успеха
 * @param {function} onError(data) - коллбек ошибки
*/

export default function sendForm(event, onSuccess, onError, options) {
  event.preventDefault();
  const $form = $(event.target);
  if ($form.data('submitted')) {
    return true;
  }
  let config = {
    url: $form.attr('action') || '',
    type: $form.attr('method') || 'GET',
    data: $form.serialize(),
  };
  if (typeof FormData !== 'undefined' && options && options.data) {
    config.contentType = false;
    config.processData = false;
    config.data = new FormData($form[0]);
  }
  config = $.extend(config, options || {});
  App.preloader.add();
  $form.data('submitted', true);
  $form.find('[type="submit"]')
    .attr('disabled', 'disabled')
    .addClass('is-disabled is-loading');

  return $.ajax(config)
    .done((response) => {
      let ajaxResponse = response;
      if (typeof response !== 'object') {
        ajaxResponse = {
          html: response,
        };
      }
      let data = $.extend(ajaxResponse, {
        form: event.target,
        html: ajaxResponse.html ? ajaxResponse.html : false,
        success: ajaxResponse.success ? ajaxResponse.success : false,
      });
      if (window.location.hostname === 'localhost') {
        data = {
          form: event.target,
          html: response,
          success: true,
          message: 'Успех',
        };
      }
      if (data.success && typeof onSuccess === 'function') {
        data = $.extend(data, onSuccess(data) || {});
      } else if (typeof onError === 'function') {
        data = $.extend(data, onError(data) || {});
      }

      if (data.redirectUrl) {
        App.redirect(data.redirectUrl);
      }
      $(document).trigger(App.triggers.ajax, data);
      $(document)
        .trigger((data.success ? App.triggers.form.success : App.triggers.form.error), data);
      return data;
    })
    .always(() => {
      $form.removeData('submitted');
      $form.find('[type="submit"]')
        .removeAttr('disabled')
        .removeClass('is-disabled is-loading');
      App.preloader.remove();
    })
    .fail(App.onAjaxFail);
}
